import React from 'react';
/*
import { useStaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
*/
import { Container } from '@styles/global';
import Fade from 'react-reveal/Fade';
import styled from 'styled-components';

const HeaderWrapper = styled.div`
    /** padding: 150px; 50px */
    overflow: hidden;
    padding: 7rem 0;

    @media (min-width: ${props => props.theme.screen.md}) {
        padding: 7rem 0 14rem 0;
    }

    h1,
    p {
        color: var(--accent-color);
    }

    h1 {
        font-weight: 900;
        letter-spacing: 2px;
        text-transform: uppercase;
    }

    img {
        filter: brightness(60%);
        margin-bottom: 0;
    }
`;

const Grid = styled(Container)`
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
`;

export const HeaderText = styled.h1`
    font-size: 2rem;
    margin-bottom: 1rem;

    @media (min-width: ${props => props.theme.screen.md}) {
        font-size: 2.5rem;
    }

    @media (min-width: ${props => props.theme.screen.lg}) {
        font-size: 4rem;

        ${props =>
            props.small &&
            `
            font-size: 3rem;       
        `};
    }
`;

export const Text = styled.h2`
    color: var(--yellow);
`;

export default function Header({ title, description, headerImage, color }) {
    return (
        <>
            <HeaderWrapper style={{ backgroundColor: color }}>
                <Grid>
                    <div>
                        <Text>we help businesses</Text>
                        <Fade top>
                            <HeaderText>innovate and grow</HeaderText>
                        </Fade>
                    </div>
                </Grid>
            </HeaderWrapper>
        </>
    );
}

Header.defaultProps = {
    title: 'innovate and grow',
    description:
        'We help to build innovative tech and grow our clients businesses through a unique and holistic approach.',
    headerImage: '',
    color: 'var(--primary-color)',
};
